import Link from "next/link";
import { useState } from "react";

const Menu = () => {
  const [toggle, setToggle] = useState(false);

  const menuStyle = e => {
    if (!e) {
      return {
        top: "3rem",
        left: "3rem",
        transition: ".5s"
      };
    } else {
      return {
        top: 0,
        left: 0,
        transition: ".5s"
      };
    }
  };

  const menuToggle = e => {
    if (!e) {
      setToggle(true);
    } else setToggle(false);
  };

  return (
    <nav className="menu" style={menuStyle(toggle)}>
      <button className="toggle" onClick={() => menuToggle(toggle)}>
        ss
      </button>
      <Link href="/">
        <a>Home</a>
      </Link>
      <Link href="/frontend">
        <a>Frontend</a>
      </Link>
      <Link href="/design">
        <a>Design</a>
      </Link>
      <Link href="/resume">
        <a>Resum√©</a>
      </Link>
    </nav>
  );
};

export default Menu;
